.tu-pagination {
  white-space: nowrap;
  padding: 2px 5px;
  color: #8394be; }
  .tu-pagination::before,
  .tu-pagination::after {
    display: table;
    content: ""; }
  .tu-pagination::after {
    clear: both; }
  .tu-pagination span:not([class*="suffix"]),
  .tu-pagination button {
    display: inline-block;
    font-size: 14px;
    min-width: 34px;
    height: 28px;
    line-height: 28px;
    box-sizing: border-box; }
  .tu-pagination .tu-select span:not([class*="suffix"]),
  .tu-pagination .tu-select button {
    min-width: 0; }
  .tu-pagination button {
    border: none;
    padding: 0 6px;
    background: transparent; }
    .tu-pagination button:focus {
      outline: none; }
    .tu-pagination button:hover {
      color: #6d5dfc; }
    .tu-pagination button:disabled {
      color: #bec8e4;
      cursor: not-allowed; }
  .tu-pagination .btn-prev,
  .tu-pagination .btn-next {
    cursor: pointer;
    margin: 0;
    color: #8394be; }
    .tu-pagination .btn-prev .tu-icon,
    .tu-pagination .btn-next .tu-icon {
      position: relative; }
  .tu-pagination .btn-prev {
    padding-right: 12px; }
  .tu-pagination .btn-next {
    padding-left: 12px; }
  .tu-pagination .tu-pager li.disabled {
    color: #c8d0e7;
    cursor: not-allowed; }
  .tu-pagination--small .btn-prev,
  .tu-pagination--small .btn-next,
  .tu-pagination--small .tu-pager li,
  .tu-pagination--small .tu-pager li.btn-quicknext,
