    visibility: hidden; }
  .tu-pagination--small .more::before,
  .tu-pagination--small li.more::before {
    line-height: 24px; }
  .tu-pagination--small span:not([class*="suffix"]),
  .tu-pagination--small button {
    vertical-align: center;
    height: 22px;
    line-height: 22px; }
