      height: 22px; }
  .tu-pagination--small .tu-pagination__editor {
    height: 22px; }
    .tu-pagination--small .tu-pagination__editor.tu-input .tu-input__inner {
      height: 22px; }
  .tu-pagination--small.is-button .tu-pager {
    margin-top: 1px; }
  .tu-pagination__sizes {
    margin: 0 10px 0 0;
    font-weight: normal;
    color: #8394be;
    width: 90px; }
    .tu-pagination__sizes .tu-input .tu-input__inner {
      font-size: 14px;
      padding-left: 8px;
      padding-right: 20px; }
      .tu-pagination__sizes .tu-input .tu-input__inner:hover {
        border-color: #6d5dfc; }
    .tu-pagination__sizes .tu-input__suffix .tu-input__icon-slot {
      width: 18px;
      height: 22px;
      line-height: 22px; }
      .tu-pagination__sizes .tu-input__suffix .tu-input__icon-slot .tu-input__icon {
        width: 18px;
        position: relative;
        top: -1px; }
  .tu-pagination__total {
    margin-right: 10px;
    font-weight: normal;
    color: #8394be; }
  .tu-pagination__jump {
    margin-left: 24px;
    font-weight: normal;
    color: #8394be; }
    .tu-pagination__jump .tu-input__inner {
