  .tu-pagination__rightwrapper {
    float: right; }
  .tu-pagination__editor {
    line-height: 18px;
    padding: 0 2px;
    height: 28px;
    text-align: center;
    margin: 0 2px;
    box-sizing: border-box;
    border-radius: 3px; }
    .tu-pagination__editor.tu-input {
