  .tu-pagination.is-button .btn-prev,
  .tu-pagination.is-button .btn-next,
  .tu-pagination.is-button .tu-pager li {
    margin: 0 5px;
    color: #8394be;
    min-width: 30px;
    border-radius: 2px;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
    .tu-pagination.is-button .btn-prev.disabled,
    .tu-pagination.is-button .btn-next.disabled,
    .tu-pagination.is-button .tu-pager li.disabled {
      color: #bec8e4; }
  .tu-pagination.is-button .btn-prev,
  .tu-pagination.is-button .btn-next {
    padding: 0; }
    .tu-pagination.is-button .btn-prev:disabled,
    .tu-pagination.is-button .btn-next:disabled {
      color: #bec8e4; }
  .tu-pagination.is-button .tu-pager li:not(.disabled):hover {
    color: #6d5dfc; }
  .tu-pagination.is-button .tu-pager li:not(.disabled).active {
