  .tu-pagination.is-button.tu-pagination--small .btn-prev,
  .tu-pagination.is-button.tu-pagination--small .btn-next,
  .tu-pagination.is-button.tu-pagination--small .tu-pager li {
    margin: 0 3px;
    min-width: 22px; }

.tu-pager {
  user-select: none;
  list-style: none;
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  padding: 0;
  margin: 0; }
  .tu-pager .more::before {
    line-height: 30px; }
  .tu-pager li {
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 14px;
    min-width: 34px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    margin: 0;
    transition: color 0.3s, opacity 0.3s; }
    .tu-pager li.btn-quicknext, .tu-pager li.btn-quickprev {
      line-height: 28px;
      color: #8394be; }
      .tu-pager li.btn-quicknext.disabled, .tu-pager li.btn-quickprev.disabled {
        color: #c8d0e7; }
    .tu-pager li.btn-quickprev:hover {
      cursor: pointer; }
    .tu-pager li.btn-quicknext:hover {
      cursor: pointer; }
    .tu-pager li.active + li {
      border-left: 0; }
    .tu-pager li:hover {
      color: #6d5dfc; }
    .tu-pager li.active {
      color: #6d5dfc;
      cursor: default; }
